import svg from "../../check.svg";

export default {
  props: {
    label: String,
    value: Boolean,
    highlight: Boolean,
    hover: Boolean
  },
  data: ()=>({
    svg,
    isChecked: false
  }),
  watch: {
    value: {
      handler(v) {
        this.isChecked = v;
      },
      immediate: true
    }
  },
  methods: {
    onClick(e) {
      this.isChecked = !this.isChecked;
      this.$emit('change', this.isChecked);
      this.$emit('input', this.isChecked);
    }
  }
};
