import checkbox from './checkbox.vue';
import svg from "../../magnify.svg";

export default {
  props: {
    image: Object,
    selectMode: Boolean
  },
  data: () => ({
    isSelected: false,
    isHovering: false,
    svg
  }),
  components: {
    checkbox
  },
  mounted() {
    this.$watch('isSelected', v => {
      this.$emit('select-change', v, this.image.index);
    });
  },
  methods: {
    onClick() {
      if(this.selectMode) {
        this.isSelected = !this.isSelected;
      } else {
        this.$emit('view', this.image);
      }
    },
    onMagnifyClick(){
      this.$emit('view', this.image);
    },
    onMouseEnter() {
      this.isHovering = true;
    },
    onMouseLeave() {
      this.isHovering = false;
    }
  }
}
